"use client";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { Search, ExternalLink, Ban, CheckCircle, AlertOctagon } from "lucide-react";

export default function ManageStoresPage() {
  const [stores, setStores] = useState<any[]>([]);
  const [search, setSearch] = useState("");
  const [loadingId, setLoadingId] = useState<string | null>(null);

  useEffect(() => {
    fetchStores();
  }, []);

  async function fetchStores() {
    const { data } = await supabase
      .from('stores')
      .select('*')
      .order('created_at', { ascending: false });
    if (data) setStores(data);
  }

  async function toggleBan(storeId: string, currentStatus: string) {
    if (!confirm(currentStatus === 'banned' ? "Activate this store?" : "Are you sure you want to BAN this vendor?")) return;
    
    setLoadingId(storeId);
    const newStatus = currentStatus === 'banned' ? 'active' : 'banned';

    const { error } = await supabase
      .from('stores')
      .update({ status: newStatus })
      .eq('id', storeId);

    if (!error) {
      setStores(stores.map(s => s.id === storeId ? { ...s, status: newStatus } : s));
    } else {
      alert("Failed to update status. Check RLS policies.");
    }
    setLoadingId(null);
  }

  const filteredStores = stores.filter(s => 
    s.name.toLowerCase().includes(search.toLowerCase()) || 
    s.slug.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-end">
        <div>
          <h2 className="text-3xl font-black text-white">Manage Vendors</h2>
          <p className="text-gray-400">Monitor compliance and take action on reports.</p>
        </div>
        
        <div className="relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500" size={18} />
          <input 
            type="text"
            placeholder="Search stores..."
            className="bg-gray-800 border border-gray-700 rounded-xl py-2 pl-10 pr-4 text-white outline-none focus:border-emerald-500 transition w-64"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
      </div>

      <div className="bg-gray-800/50 border border-gray-700 rounded-3xl overflow-hidden">
        <table className="w-full text-left">
          <thead className="bg-gray-900/50 text-gray-400 text-xs uppercase font-bold">
            <tr>
              <th className="px-6 py-4">Store Details</th>
              <th className="px-6 py-4">Plan</th>
              <th className="px-6 py-4">Status</th>
              <th className="px-6 py-4">Actions</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-700/50">
            {filteredStores.map((store) => (
              <tr key={store.id} className={`hover:bg-white/5 transition ${store.status === 'banned' ? 'bg-red-900/10' : ''}`}>
                <td className="px-6 py-4">
                    <p className="font-bold text-white">{store.name}</p>
                    <p className="text-xs text-gray-500">/{store.slug}</p>
                </td>
                <td className="px-6 py-4">
                   <span className="text-xs font-bold text-gray-400 uppercase">{store.subscription_plan}</span>
                </td>
                <td className="px-6 py-4">
                  {store.status === 'banned' ? (
                    <div className="flex items-center gap-2 text-red-500 font-bold text-xs uppercase bg-red-500/10 px-3 py-1 rounded-full w-fit">
                        <AlertOctagon size={14}/> Banned
                    </div>
                  ) : (
                    <div className="flex items-center gap-2 text-emerald-500 font-bold text-xs uppercase bg-emerald-500/10 px-3 py-1 rounded-full w-fit">
                        <CheckCircle size={14}/> Active
                    </div>
                  )}
                </td>
                <td className="px-6 py-4 flex items-center gap-3">
                  <a href={`/${store.slug}`} target="_blank" className="p-2 bg-gray-800 hover:bg-gray-700 rounded-lg text-gray-400 hover:text-white" title="View Store">
                    <ExternalLink size={18} />
                  </a>
                  
                  <button 
                    onClick={() => toggleBan(store.id, store.status)}
                    disabled={loadingId === store.id}
                    className={`p-2 rounded-lg transition text-white ${
                      store.status === 'banned' 
                        ? 'bg-emerald-600 hover:bg-emerald-700' 
                        : 'bg-red-600 hover:bg-red-700'
                    }`}
                    title={store.status === 'banned' ? "Unban Store" : "Ban Store"}
                  >
                     {loadingId === store.id ? "..." : <Ban size={18} />}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}